
import React from "react";
import PropTypes from "prop-types";
import * as d3 from "d3";
import { curveMonotoneX } from "d3-shape";
import { format } from "d3-format";
import { timeFormat } from "d3-time-format";

import { ChartCanvas, Chart,ZoomButtons } from "react-stockcharts";
import {
	BarSeries,
	CandlestickSeries,
    BollingerSeries,
	LineSeries,
	MACDSeries,
    KagiSeries,
} from "react-stockcharts/lib/series";
import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import {
	CrossHairCursor,
	EdgeIndicator,
	CurrentCoordinate,
	MouseCoordinateX,
	MouseCoordinateY,
} from "react-stockcharts/lib/coordinates";
import { SampleData } from "../../../static/Stockdata";

import { discontinuousTimeScaleProvider, discontinuousTimeScaleProviderBuilder } from "react-stockcharts/lib/scale";
import {
	OHLCTooltip,
	MovingAverageTooltip,
	MACDTooltip,
    BollingerBandTooltip
} from "react-stockcharts/lib/tooltip";
import { ema, macd, heikinAshi, sma, kagi, bollingerBand } from "react-stockcharts/lib/indicator";
import { fitWidth } from "react-stockcharts/lib/helper";
import { TrendLine, DrawingObjectSelector } from "react-stockcharts/lib/interactive";
import { last, toObject } from "react-stockcharts/lib/utils";

import {
	saveInteractiveNodes,
	getInteractiveNodes,
} from "./interactiveutils";

const macdAppearance = {
	stroke: {
		macd: "orange",
		signal: "blue",
	},
	fill: {
		divergence: "#4682B4"
	},
};

const candlesAppearance = {
    wickStroke: function fill(d) {
        return d.close > d.open ? "#26a69a" : "#ef5350";
      },
    fill: function fill(d) {
      return d.close > d.open ? "#26a69a" : "#ef5350";
    },
    stroke: function fill(d) {
        return d.close > d.open ? "#26a69a" : "#ef5350";
      },
    candleStrokeWidth: 1,
    widthRatio: 0.8,
    opacity: 1,
  }
  const bbStroke = {
	top: "#964B00",
	middle: "#000000",
	bottom: "#964B00",
};

const bbFill = "#4682B4";

class CandlestickChart extends React.Component {
	constructor(props) {
		super(props);
		this.onKeyPress = this.onKeyPress.bind(this);
		this.onDrawCompleteChart1 = this.onDrawCompleteChart1.bind(this);
		this.onDrawCompleteChart3 = this.onDrawCompleteChart3.bind(this);
		this.handleSelection = this.handleSelection.bind(this);

		this.saveInteractiveNodes = saveInteractiveNodes.bind(this);
		this.getInteractiveNodes = getInteractiveNodes.bind(this);

		this.saveCanvasNode = this.saveCanvasNode.bind(this);
        this.handleReset = this.handleReset.bind(this);
	

		this.state = {
			enableTrendLine: false,
			trends_1: [
				{ start: [1606, 56], end: [1711, 53], appearance: { stroke: "green" }, type: "XLINE" }
			],
			trends_3: []
		};
	}
	saveCanvasNode(node) {
		this.canvasNode = node;
	}
	componentDidMount() {
		document.addEventListener("keyup", this.onKeyPress);
	}
	componentWillUnmount() {
		document.removeEventListener("keyup", this.onKeyPress);
        
	}
	handleSelection(interactives) {
		const state = toObject(interactives, each => {
			return [
				`trends_${each.chartId}`,
				each.objects,
			];
		});
		this.setState(state);
	}
	onDrawCompleteChart1(trends_1) {
		// this gets called on
		// 1. draw complete of trendline
		// 2. drag complete of trendline
		console.log(trends_1);
		this.setState({
			enableTrendLine: false,
			trends_1
		});
	}
	onDrawCompleteChart3(trends_3) {
		// this gets called on
		// 1. draw complete of trendline
		// 2. drag complete of trendline
		console.log(trends_3);
		this.setState({
			enableTrendLine: false,
			trends_3
		});
	}
	onKeyPress(e) {
		const keyCode = e.which;
		console.log(keyCode);
		switch (keyCode) {
		case 46: { // DEL

			const trends_1 = this.state.trends_1
				.filter(each => !each.selected);
			const trends_3 = this.state.trends_3
				.filter(each => !each.selected);

			this.canvasNode.cancelDrag();
			this.setState({
				trends_1,
				trends_3,
			});
			break;
		}
		case 27: { // ESC
			this.node_1.terminate();
			this.node_3.terminate();
			this.canvasNode.cancelDrag();
			this.setState({
				enableTrendLine: false
			});
			break;
		}
		case 68:   // D - Draw trendline
		case 69: { // E - Enable trendline
			this.setState({
				enableTrendLine: true
			});
			break;
		}
		}
	}

    handleReset() {
		this.setState({
			suffix: this.state.suffix + 1
		});
	}

	render() {
        const bb = bollingerBand()
			.merge((d, c) => {d.bb = c;})
			.accessor(d => d.bb);


		const ema26 = ema()
			.id(0)
			.options({ windowSize: 26 })
			.merge((d, c) => { d.ema26 = c; })
			.accessor(d => d.ema26);

		const ema12 = ema()
			.id(1)
			.options({ windowSize: 12 })
			.merge((d, c) => {d.ema12 = c;})
			.accessor(d => d.ema12);

		const macdCalculator = macd()
			.options({
				fast: 12,
				slow: 26,
				signal: 9,
			})
			.merge((d, c) => {d.macd = c;})
			.accessor(d => d.macd);

		const { width, stockdata} = this.props;
        const dateTimeFormat = "%Y-%m-%d %H:%M:%S";
        const parseDate = d3.timeParse(dateTimeFormat);
		const calculatedData = macdCalculator(ema12(ema26(stockdata)));
		const xScaleProvider = discontinuousTimeScaleProviderBuilder().inputDateAccessor(
            (d) => parseDate(d.day)
            );
		const {
			data,
			xScale,
			xAccessor,
			displayXAccessor,
		} = xScaleProvider(calculatedData);

		const start = xAccessor(last(data));
		const end = xAccessor(data[Math.max(0, data.length - 150)]);
		const xExtents = [start, end];

        const volumeSeries = (data) => {
            return parseFloat(data.volumn);
          };

		return (
			<ChartCanvas ref={this.saveCanvasNode}
                ratio={1}
				height={600}
				width={width}	
				margin={{ left: 70, right: 70, top: 20, bottom: 30 }}
				data={data}
				xScale={xScale}
				xAccessor={xAccessor}
				displayXAccessor={displayXAccessor}
				xExtents={xExtents}
                mouseMoveEvent = {true}
                zoomEvent={false}
                clamp ={true}
			>
				<Chart id={1} height={400}
					yExtents={[d => [d.high, d.low], ema26.accessor(), ema12.accessor()]}
					padding={{ top: 10, bottom: 20 }}
    
				>
					<XAxis axisAt="bottom" orient="bottom" showTicks={false} outerTickSize={0} />
					<YAxis axisAt="right" orient="right" ticks={5}

                     />
					<MouseCoordinateY
						at="right"
						orient="right"
						displayFormat={format(".2f")} />

					<CandlestickSeries  {...candlesAppearance} />
{/* 
                    <BollingerSeries yAccessor={d => d.bb}
						stroke={bbStroke}
						fill={bbFill} /> */}

					<LineSeries yAccessor={ema26.accessor()} stroke={ema26.stroke()}/>
					<LineSeries yAccessor={ema12.accessor()} stroke={ema12.stroke()}/>

					<CurrentCoordinate yAccessor={ema26.accessor()} fill={ema26.stroke()} />
					<CurrentCoordinate yAccessor={ema12.accessor()} fill={ema12.stroke()} />

					<EdgeIndicator itemType="last" orient="right" edgeAt="right"
						yAccessor={d => d.close} fill={d => d.close > d.open ? "#26a69a" : "#ef5350"}/>

					<OHLCTooltip origin={[-40, 0]}/>
                   

					<MovingAverageTooltip
						onClick={e => console.log(e)}
						origin={[-38, 15]}
						options={[
							{
								yAccessor: ema26.accessor(),
								type: ema26.type(),
								stroke: ema26.stroke(),
								windowSize: ema26.options().windowSize,
							},
							{
								yAccessor: ema12.accessor(),
								type: ema12.type(),
								stroke: ema12.stroke(),
								windowSize: ema12.options().windowSize,
							},
						]}
					/>
                    {/* <BollingerBandTooltip
						origin={[-38, 60]}
						yAccessor={d => d.bb}
						options={bb.options()} /> */}

                    <ZoomButtons
						onReset={this.handleReset}
					/>
                    
					<TrendLine
						ref={this.saveInteractiveNodes("Trendline", 1)}
						enabled={this.state.enableTrendLine}
						type="RAY"
						snap={false}
						snapTo={d => [d.high, d.low]}
						onStart={() => console.log("START")}
						onComplete={this.onDrawCompleteChart1}
						trends={this.state.trends_1}
					/>
                     
				</Chart>




				<Chart id={2} height={150}
					yExtents={volumeSeries}
					origin={(w, h) => [0, h - 300]}
				>
					<YAxis axisAt="left" orient="left" ticks={5} tickFormat={format(".2s")}/>

					<MouseCoordinateY
						at="left"
						orient="left"
						displayFormat={format(".4s")} />
					<BarSeries yAccessor={volumeSeries} fill={d => d.close > d.open ? "rgba(38, 166, 154, 0.3)" : "rgba(239, 83, 80, 0.3)"} />
				</Chart>





				<Chart id={3} height={150}
					yExtents={macdCalculator.accessor()}
					origin={(w, h) => [0, h - 150]} padding={{ top: 10, bottom: 10 }}
				>
					<XAxis axisAt="bottom" orient="bottom"/>
					<YAxis axisAt="right" orient="right" ticks={2} />

					<MouseCoordinateX
						at="bottom"
						orient="bottom"
						displayFormat={timeFormat("%Y-%m-%d")} />
					<MouseCoordinateY
						at="right"
						orient="right"
						displayFormat={format(".2f")} />
					<TrendLine
						ref={this.saveInteractiveNodes("Trendline", 3)}
						enabled={this.state.enableTrendLine}
						type="RAY"
						snap={false}
						snapTo={d => [d.high, d.low]}
						onStart={() => console.log("START")}
						onComplete={this.onDrawCompleteChart3}
						trends={this.state.trends_3}
					/>
					<MACDSeries yAccessor={d => d.macd}
						{...macdAppearance} />
					<MACDTooltip
						origin={[-38, 15]}
						yAccessor={d => d.macd}
						options={macdCalculator.options()}
						appearance={macdAppearance}
					/>
				</Chart>
				<CrossHairCursor />
				<DrawingObjectSelector
					enabled={!this.state.enableTrendLine}
					getInteractiveNodes={this.getInteractiveNodes}
					drawingObjectMap={{
						Trendline: "trends"
					}}
					onSelect={this.handleSelection}
				/>
			</ChartCanvas>
		);
	}
}


CandlestickChart.defaultProps = {
	type: "svg",
};

const CandleStickChart = fitWidth(CandlestickChart);

export default CandleStickChart;
